___CSS_LOADER_EXPORT___.push([module.id, `.product-preview {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 16px;
    text-align: center;
    max-width: 200px;
    margin: 10px;

    display: flex;
    flex-direction: column;
}

.product-image {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
}

.product-name {
    font-size: 18px;
    margin: 10px 0;
}

.product-description {
    font-size: 14px;
    color: #666;
    margin: 10px 0;
}

.product-price {
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0;
}

.cart-button-container {
    display: flex;
    justify-content: center;
    margin-top: auto;
}
